const recursiveKeyFilter = require('recursive-key-filter');

/**
 * Remove __typename from a GraphQL response or object.
 *
 * @param {Object|Array} obj object to remove __typename from
 * @return {Object} object same as input but without the __typename
 */
function removeTypename(obj) {
  if (typeof obj !== 'object') {
    throw new Error('Provided input is not an object or array.');
  }

  return recursiveKeyFilter(obj, ['__typename']);
}

module.exports = removeTypename;
